'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import Image from 'next/image'
import TextVideoHover from '@/components/TextVideoHover'

export default function Header() {
  const [isMobileNavActive, setIsMobileNavActive] = useState(false)
  const [theme, setTheme] = useState('dark');

  useEffect(() => {
    const savedTheme = localStorage.getItem('theme') || 'dark';
    setTheme(savedTheme);
    document.documentElement.setAttribute('data-theme', savedTheme);
  }, []);

  const toggleTheme = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
    localStorage.setItem('theme', newTheme);
    document.documentElement.setAttribute('data-theme', newTheme);
  };

  const toggleMobileNav = () => {
    setIsMobileNavActive(!isMobileNavActive)
  }

  return (
    <header id="header" className="header d-flex align-items-center fixed-top">
      <div className="header-container container-fluid container-xl position-relative d-flex align-items-center justify-content-between">

        <Link href="/" className="logo d-flex align-items-center me-auto me-xl-0">
          <Image 
            src="/assets/img/SIGNVRSE LOGO COLOR.png" 
            alt="Signvrse" 
            width={150}
            height={50}
            priority
            loading="eager"
            style={{ width: 'auto', height: 'auto' }}
          />
        </Link>

        <nav id="navmenu" className={`navmenu ${isMobileNavActive ? 'mobile-nav-active' : ''}`}>
          <ul>
          <TextVideoHover videoSrc="/assets/img/translated/Home.mp4">
            <li><Link href="/#hero" className="active">Home</Link></li>
          </TextVideoHover>
            <li className="dropdown">
              <Link href="/whoweare"><span>Who We Are</span> <i className="bi bi-chevron-down toggle-dropdown"></i></Link>
              <ul>
                <li><Link href="/whoweare">About</Link></li>
                <li><Link href="/whoweare">Team</Link></li>
                <li><Link href="/whoweare">Advisory Board</Link></li>
              </ul>
            </li>
      
            <li className="dropdown">
              <Link href="/whatwedo"><span>What We Do</span> <i className="bi bi-chevron-down toggle-dropdown"></i></Link>
              <ul>
                <li><Link href="/whatwedo">Track Record</Link></li>
                <li><Link href="/whatwedo">Solutions</Link></li>
                <li><Link href="/whatwedo">Pricing</Link></li>
              </ul>
            </li>
      
            <li className="dropdown">
              <Link href="/case-studies"><span>Case Studies</span> <i className="bi bi-chevron-down toggle-dropdown"></i></Link>
              <ul>
                <li><Link href="/case-studies">Projects</Link></li>
                <li><Link href="/case-studies">Client Stories</Link></li>
              </ul>
            </li>
      
            <li className="dropdown">
              <Link href="/partnerships"><span>Partnerships</span> <i className="bi bi-chevron-down toggle-dropdown"></i></Link>
              <ul>
                <li><Link href="/partnerships">How to Partner</Link></li>
                <li><Link href="/partnerships">Current Partners</Link></li>
              </ul>
            </li>
      
            <li><Link href="/#contact">Contact</Link></li>
          </ul>
        </nav>

        <a 
          className="btn-getstarted" 
          href="https://calendly.com/signvrse/30min" 
          target="_blank" 
          rel="noopener noreferrer"
        >
          Book a Demo
        </a>

        <i 
          className={`mobile-nav-toggle d-xl-none bi ${isMobileNavActive ? 'bi-x' : 'bi-list'}`}
          onClick={toggleMobileNav}
        ></i>

        <button onClick={toggleTheme} className="theme-toggle">
          <i className={`bi ${theme === 'light' ? 'bi-moon' : 'bi-sun'}`}></i>
        </button>

      </div>
    </header>
  )
}
